import React from "react";
import { motion } from "framer-motion";
import "./style.css";

const AniButton = () => {
  return (
    <div className="example-container">
      <motion.div
        initial={{ x: "60%" }}
        animate={{
          backgroundColor: "#F68C1E",
          boxShadow: "10px 10px 0 rgba(0, 0, 0, 0.2)",
          position: "center",
        }}
        whileHover={{ scale: 1.2 }}
        whileTap={{ scale: 0.8 }}
        style={{ x: "60%" }}
        onHoverStart={(e) => {}}
        onHoverEnd={(e) => {}}
      >
        <p> An animated text </p>
      </motion.div>
    </div>
  );
};
export default AniButton;
